:root{
    --borderimg: 10px solid var(--theBigGrey);
    --borderColor: var(--theBigGrey);
}
.projet-img{
    width: 100%;
    height: 325px;
    border: 5px solid var(--borderColor);
}

.pictures{
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
}

.projet-img-text{
    font-size: 22px;
    background-color: white;
    color: black;
    border: 5px solid var(--borderColor);
    width: 100%;
}

.ImageSpan{
    width: 400px;
    border: var(--borderimg);
    background-color: var(--blueNav);
    padding: 20px;
    text-align: center;
}