:root{
    --blueNav: rgb(3, 8, 33);
    --theBigGrey: grey;
}

body{
    margin: 0;
    padding: 0;
}

.toCenter{
    text-align: center;
}


.App{
    background-color: var(--theBigGrey);
    color: rgb(255, 255, 255);
}

.app-content{
    display: flex;
    flex-direction: column;
}

.notbold{
    font-weight: normal;
}

.portfolio-img{
    width: 300px;
    height: 200px;
    border: var(--borderimg);

}

.totalFlexCol{
    display: flex;
    flex-direction: column;
    flex-wrap: wrap;
}

.center{
    text-align: center;
}

.GlobalSize{
    padding: 20px;
}