.ordi{
    width: 71px;
    height: 40px;
    border: 5px solid transparent;
}

.nav-content{
    list-style: none;
    display: flex;
    flex-wrap: wrap;
}

.border{
    border: 15px solid transparent;
}

.nav-link{
    padding: 10px;
    text-decoration: none;
    color: white;
}

.nav-link:hover{
    background-color: var(--theBigGrey);
}

.Navbar{
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;
    border: 2px solid transparent;
    background-color: var(--blueNav);
}